.container-form {
  display: flex;
  justify-content: center;
  padding: 3rem 0;
}

.box {
  width: 300px;
  padding: 2rem 2rem;
  border: 2px solid whitesmoke;
  border-radius: 5px;
}

.box:hover {
  box-shadow: 2px 0.1px 2px 2px whitesmoke;
}

.input-inner {
  height: 2.5rem;
  width: 100%;
  margin-bottom: 1rem;
  border: 1px solid rgb(226, 226, 226);
  border-radius: 5px;
}

.btn {
  width: 150px;
  padding: 0.5rem 1rem;
  font-size: 1.5em;
  background-color: rgb(109, 10, 10);
  border: none;
  border-radius: 10px;
  float: right;
  color: whitesmoke;
}

.btn:hover {
  background-color: rgb(182, 22, 22);
}

.add-contact {
  margin-bottom: 1rem;
  text-align: center;
  color: rgb(32, 35, 42);
}

@media screen and (max-width: 820px) {
  .box {
    width: 250px;
    padding: 2rem 2rem;
    border: 2px solid whitesmoke;
    border-radius: 5px;
  }
}

@media screen and (max-width: 568px) {
  .box {
    width: 220px;
    padding: 2rem 2rem;
    border: 2px solid whitesmoke;
    border-radius: 5px;
  }
}
