.container {
  margin: 1rem 2rem;
}

table {
  border-collapse: collapse;
  width: 100%;
}

th,
td {
  text-align: left;
  padding: 1rem 2rem;
  border: 0.2px solid whitesmoke;
}

th:first-child {
  width: 30%;
}

td:first-child {
  width: 90%;
}

th {
  background-color: gainsboro;
  color: rgb(32, 35, 42);
  font-size: 1.2em;
  padding: none;
}

.search-field {
  display: flex;
  flex-direction: row;
  background-color: white;
  border-radius: 2rem;
  height: 2rem;
  padding-left: 1rem;
}

.search-field:hover {
  box-shadow: 1px 0.1px 1px 1px whitesmoke;
}

.search-field-item:first-child {
  margin-right: 0.2rem;
  margin-top: 4px;
}

.search-field-icon {
  color: gray;
  font-size: 0.7em;
}

.input-contacts {
  width: 90%;
  height: 2rem;
  font-size: 0.9em;
  border: none;
}
input:focus {
  outline: none;
}

.contact-down {
  color: blue;
}

.contact-delete {
  float: right;
  color: red;
}

.contact-edit {
  color: blue;
}

.container-inner,
.container-inner-li {
  padding: 1rem 1rem;
  list-style: none;
  background-color: rgb(32, 35, 42);
  color: white;
}

.container-inner-content {
  margin-left: 2rem;
  color: goldenrod;
}

.contact-details {
  margin-top: 1rem;
}

@media screen and (max-width: 820px) {
  .container {
    margin: 1rem 1rem;
  }

  td:first-child {
    width: 70%;
  }
}
